@use '../../shared/style/mixin.scss' as mix;
@use '../../shared/style/variable.scss' as var;

.Cart {
	border-radius: 18px;
	padding: 24px 16px;
	width: 300px;
	background-color: var.$color-white;
	.wrapper_cart {
		margin-bottom: 12px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.title {
			margin: 0;
			font-weight: var.$font-weight-semibold;
			font-size: var.$font-size-large;
			line-height: var.$line-height-small;
		}
		.count {
			margin: 0;
			padding: 2px 14px;
			font-size: var.$font-size-small;
			background-color: var.$color-gray-dark-light;
			border-radius: 6px;
			line-height: var.$line-height-medium;
		}
	}
	.carts {
		@include mix.list-reset();
		> * {
			border-bottom: 1px solid #f2f2f3;
		}
		> :first-child {
			border-top: 1px solid #f2f2f3;
		}
	}
	.wrapper_total {
		margin-bottom: 24px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.text {
			margin: 0;
			line-height: var.$line-height-large;
		}
		.total {
			margin: 0;
			line-height: var.$line-height-large;
		}
	}
	.button {
		margin-bottom: 8px;
		@include mix.button-style(
			var.$button-bg-default-2,
			var.$color-white,
			var.$button-bg-hover-1,
			var.$button-bg-selected
		);
	}
	.info {
		position: relative;
		margin: 0 0 0 32px;
		&:before {
			@include mix.absoluteCenter();
			content: '';
			background-image: url('../../shared/assets/icon/delivery.png');
			width: 24px;
			height: 24px;
			left: -32px;
		}
	}
	.loader {
		@include mix.loader();
	}
}
